// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
import VueRouter from 'vue-router';
import sliderView from './components/sliderView';
//import changjing from './components/changjing';
//import huodong from './components/huodong';
//import pinpai from './components/pinpai';
//import zuopin from './components/zuopin';
//import yuyue from './components/yuyue';
//import zhengwen from './components/zhengwen';


Vue.config.productionTip = false;

Vue.use(VueRouter);

var router = new VueRouter({
//	mode: 'history',
	routes:	[
	{
		path: '/',
		component: sliderView
	},
	{
		path: '/changjing',
		component: (resolve) => require(['./components/changjing'],resolve)
	},
	{
		path: '/huodong',
		component: (resolve) => require(['./components/huodong'],resolve)
	},
	{
		path: '/pinpai',
		component: (resolve) => require(['./components/pinpai'],resolve)
	},
	{
		path: '/yuyue',
		component: (resolve) => require(['./components/yuyue'],resolve)
	},
	{
		path: '/zuopin',
		component: (resolve) => require(['./components/zuopin'],resolve)
	},
	{
		path: '/zuopin/zhengwen',
		component: (resolve) => require(['./components/zhengwen'],resolve)
	}
	]
});

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



