<template>
	<div id="IndexBGSlider" class="flexslider-bgslider">
				<ul class="slides">
					<!-- 配置slider组件 -->
					<slider ref="slider" :pages="pages" :sliderinit="sliderinit" @slide='slide' @tap='onTap' @init='onInit'>
						<!-- 设置loading,可自定义 -->
						<!--<div slot="loading">loading...</div>-->
					</slider>
				</ul>
			</div>
</template>

<script>
	import slider from 'vue-concise-slider' // import slider components
	export default {
		name: 'slider-view',
//		el: '#app',
		data() {
			return {
				//Image list
				pages: [{
						html: `<li>
                    	<a href="plus/list.php@tid=63" title="客照">
                    		<img src="/static/uploads/allimg/131206/1-1312061A3330-L.jpg" alt="客照" title="客照" border="0" />
                        </a>
                    </li>
                    `,
						style: {
//							'background': '#1bbc9b'
						}
					},
					{
						html: `<li>
                    	<a href="plus/list.php@tid=60" title="内景">
                    		<img src="/static/uploads/allimg/131206/1-1312061A3150-L.jpg" alt="内景" title="内景" border="0" />
                        </a>
                    </li>`,
						style: {
//							'background': '#4bbfc3'
						}
					},
					{
						html: `<li>
                    	<a href="plus/list.php@tid=61" title="外景">
                    		<img src="/static/uploads/allimg/131206/1-1312061A2550-L.jpg" alt="外景" title="外景" border="0" />
                        </a>
                    </li>`,
						style: {
//							'background': '#7baabe'
						}
					},
					{
						html: `<li>
                    	<a href="plus/view.php@aid=444" title="品牌">
                    		<img src="/static/uploads/allimg/131206/1-1312061A1270-L.jpg" alt="品牌" title="品牌" border="0" />
                        </a>
                    </li>`,
						style: {
//							'background': '#7baabe'
						}
					}
				],
				//Sliding configuration [obj]
				sliderinit: {
					currentPage: 0,
					thresholdDistance: 100,
					thresholdTime: 500,
					autoplay: 3000,
					loop: true,
					//        direction:'vertical',
					infinite: 1,
					slidesToScroll: 1,
					timingFunction: 'ease',
					duration: 300
				}
			}
		},
		components: {
			slider,
		},
		methods: {
			// Listener event
			slide(data) {
//				console.log(data)
			},
			onTap(data) {
//				console.log(data)
			},
			onInit(data) {
//				console.log(data)
			}
		}
	}
</script>

<style>
</style>


